(function() {
    'use strict';

    angular
        .module('app.auth')
        .factory('authService', UserService);

    /** @ngInject **/
    function UserService($q, PermRoleStore, jwtHelper, $http, api, $rootScope) {
        var currentUser = JSON.parse(sessionStorage.getItem('user'));
        $rootScope.currentUser = currentUser;
        var jwt = sessionStorage.getItem('jwt');

        var service = {
            getCurrentUser: getCurrentUser,
            checkSession: checkSession,
            refreshToken: refreshToken,
            hasPermission: hasPermission,
            storeUser: storeUser
        };

        return service;

        ///////////////

        function getCurrentUser() {
            return currentUser;
        }

        function checkSession() {
            var jwt = sessionStorage.getItem('jwt');
            if(!jwt) return false;
            if(jwtHelper.isTokenExpired(jwt)) return 'expired';
            return true;
        }

        function refreshToken() {
            var deferred = $q.defer();
            var promisse = $http({
                url: api + 'user/authentication/refresh',
                skipAuthorization: true,
                method: 'GET',
                headers: {Authorization: 'Bearer ' + jwt},
            });
            promisse.then(function (response) {
                sessionStorage.setItem('jwt', response.data.token);
                deferred.resolve(response.data.token);
            }, function () {
                sessionStorage.removeItem('jwt');
                deferred.reject();
            });
            return deferred.promise;
        }

        function hasPermission(roleDef) {
            var deferred = $q.defer();
            var hasPermission = false;

            // check if user has permission via its roles
            if(PermRoleStore.hasRoleDefinition(currentUser.rol)) {
                // check if the permission we are validating is in this role's permissions
                if(currentUser.rol == roleDef) {
                    hasPermission = true;
                }
            }

            // if we have permission resolve otherwise reject the promise
            if(hasPermission) {
                deferred.resolve();
            }
            else {
                deferred.reject();
            }

            // return promise
            return deferred.promise;
        }

        function storeUser(jwt, user) {
            sessionStorage.setItem('jwt', jwt);
            sessionStorage.setItem("user", JSON.stringify(user));
            currentUser = user;
            $rootScope.currentUser = currentUser;
            return currentUser;
        }
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.auth')
        .config(config);

    /** @ngInject */
    function config($httpProvider, jwtOptionsProvider, api_domain) {

        jwtOptionsProvider.config({
            whiteListedDomains: [api_domain],
            tokenGetter: tokenGetter
        });

        /** @ngInject */
        function tokenGetter(options, jwtHelper, authService, api) {
            var jwt = sessionStorage.getItem('jwt');
            if (jwt && options.url.indexOf(api) === 0) {
                if (jwtHelper.isTokenExpired(jwt)) {
                    authService.refreshToken().then(function (jwt) {
                        return jwt;
                    }, function () {
                        return null;
                    })
                } else {
                    return jwt;
                }
            }
        }
        $httpProvider.interceptors.push('jwtInterceptor');
    }
})();

